import '../../style.css';
import * as THREE from "three";
import { TrackballControls } from 'three/examples/jsm/controls/TrackballControls';
import {addCoordSystem} from "../../../static/lib/wfa-coord.js";
import GUI from 'lil-gui'; 
import {createKranMesh,} from "./kranHelper.js";

//globale variabler
let g_scene, g_renderer, g_camera, g_clock, g_controls, g_currentlyPressedKeys = [], g_lilGui;

let g_rotationVal = 10000;
//starter
await main();

export async function main() {
	 
	const canvas = document.createElement('canvas');
	document.body.appendChild(canvas);
	
	g_renderer = new THREE.WebGLRenderer({canvas: canvas, antialias: true});
	g_renderer.setSize(window.innerWidth, window.innerHeight);    
	g_renderer.setClearColor(0xBFD104, 0xff);  
	g_renderer.shadowMap.enabled = true; 
	g_renderer.shadowMapSoft = true;
	g_renderer.shadowMap.type = THREE.PCFSoftShadowMap; //THREE.BasicShadowMap;
	
	g_scene = new THREE.Scene();
	g_scene.background = new THREE.Color( 0xEEEEEE );	

	g_lilGui = new GUI();
	
	await addSceneObjects();
	addLights(); 
	
	g_camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000); 
	g_camera.position.x = 230;
	g_camera.position.y = 400;
	g_camera.position.z = 350;	
	g_camera.up = new THREE.Vector3(0, 1, 0);
	let target = new THREE.Vector3(0.0, 0.0, 0.0);
	g_camera.lookAt(target);
	
	g_controls = new TrackballControls(g_camera, g_renderer.domElement);
	g_controls.addEventListener( 'change', renderScene);
	
	g_clock = new THREE.Clock();
	
	window.addEventListener('resize', onWindowResize, false);
	document.addEventListener('keyup', handleKeyUp, false);
	document.addEventListener('keydown', handleKeyDown, false);
	
	animate(0); 
}

function handleKeyUp(event) {
	g_currentlyPressedKeys[event.code] = false;
}

function handleKeyDown(event) {
	g_currentlyPressedKeys[event.code] = true;
}

async function addSceneObjects() {   
	 
	//koordinatsystem 
	addCoordSystem(g_scene);
    
	//plan
	const loader = new THREE.TextureLoader();
    const texturePlane = await loader.loadAsync('../../../assets/textures/plane.jpg');
	let gPlane = new THREE.PlaneGeometry(500, 500, 10, 10);
	//let mPlane = new THREE.MeshLambertMaterial({ color: 0x999999, side: THREE.DoubleSide, wireframe:false });
	let mPlane = new THREE.MeshBasicMaterial({ map: texturePlane, side: THREE.DoubleSide });
	let meshPlane = new THREE.Mesh(gPlane, mPlane);
	meshPlane.rotation.x = Math.PI / 2;
	meshPlane.receiveShadow = true;	 
	g_scene.add(meshPlane);
	
   //modellen - kranMesh
   let kranMesh = await createKranMesh();
   kranMesh.name = "kranMesh";   
   g_scene.add(kranMesh);   
}

//light
function addLights() {

	//Lyskilde 1: ambientLight
	let ambientLight = new THREE.AmbientLight(0xffffff, 0.3);
	ambientLight.visible = true;
	g_scene.add(ambientLight);
	//controls
	const ambientFolder = g_lilGui.addFolder( 'Ambient Light' );
	ambientFolder.add(ambientLight, 'visible').name("On/Off");
	ambientFolder.add(ambientLight, 'intensity').min(0).max(1).step(0.01).name("Intensity");
	ambientFolder.addColor(ambientLight, 'color').name("Color");
	
	//Lyskilde 2: directionalLight
	let directionalLight = new THREE.DirectionalLight(0xffffff, 1.0); 
	directionalLight.visible = true;
	directionalLight.position.set(0, 300, 0);
	directionalLight.castShadow = true;
	directionalLight.shadow.camera.near = 0;
	directionalLight.shadow.camera.far = 301;
	directionalLight.shadow.camera.left = -50;
	directionalLight.shadow.camera.right = 50;
	directionalLight.shadow.camera.top = 250;
	directionalLight.shadow.camera.bottom = -250;
	directionalLight.shadow.camera.visible = true;
	g_scene.add(directionalLight);
	//controls
	const directionalFolder = g_lilGui.addFolder( 'Directional Light' );
	directionalFolder.add(directionalLight, 'visible').name("On/Off");
	directionalFolder.add(directionalLight, 'intensity').min(0).max(1).step(0.01).name("Intensity");
	directionalFolder.addColor(directionalLight, 'color').name("Color");

	//hjelpeklasse for å vise lysets utstrekning
	/*
	let lightCamHelper = new THREE.CameraHelper( directionalLight.shadow.camera );
	g_scene.add( lightCamHelper );	
	*/  

	//Styrehus: 1 spotLight	 
	const spotLight1 = new THREE.SpotLight(0x78ff00, 0.5, 10, Math.PI*0.1, 0, 0);
	spotLight1.position.set(35, 100, 25);
	g_scene.add(spotLight1);
	//lil-gui kontroller
	const spotLight1Folder = g_lilGui.addFolder( 'Spotlight 1' );
	spotLight1Folder.add(spotLight1, 'visible').name("On/Off");
	spotLight1Folder.add(spotLight1, 'intensity').min(0).max(1).step(0.01).name("Intensity");
	spotLight1Folder.addColor(spotLight1, 'color').name("Color");
    //helper
    //const spotLightHelper1 = new THREE.SpotLightHelper( spotLight1 );
	//g_scene.add( spotLightHelper1 );

	//Styrehus: 2 spotLight	 
	const spotLight2 = new THREE.SpotLight(0x78ff00, 0.5, 10, Math.PI*0.1, 0, 0);
	spotLight2.position.set(35, 100, 10);
	g_scene.add(spotLight2);
	//lil-gui kontroller
	const spotLight2Folder = g_lilGui.addFolder( 'Spotlight 2' );
	spotLight2Folder.add(spotLight2, 'visible').name("On/Off");
	spotLight2Folder.add(spotLight2, 'intensity').min(0).max(1).step(0.01).name("Intensity");
	spotLight2Folder.addColor(spotLight2, 'color').name("Color");
    //helper
    //const spotLightHelper2 = new THREE.SpotLightHelper( spotLight2 );
	//g_scene.add( spotLightHelper2 );
}

function animate(currentTime) {
	window.requestAnimationFrame((currentTime) => {
		animate(currentTime);
	});

	let delta = g_clock.getDelta();

	let part0Mesh = g_scene.getObjectByName("part0");
	part0Mesh.position.x = part0Mesh.animation.posX
	part0Mesh.rotation.y = part0Mesh.animation.angle

	//modellen kranMesh 
	let kranMesh = g_scene.getObjectByName("kranMesh");
	kranMesh.scale.set(5,5,5); 
    //animation: roter part2Mesh - Styrehus + base
	let part2Mesh = g_scene.getObjectByName("part2");
	part2Mesh.rotation.y = part2Mesh.animation.angle;
    let group2Mesh = g_scene.getObjectByName("group2");
    group2Mesh.rotation.y = part2Mesh.animation.angle;

	//animation: roter part7Mesh - Kran Arm
	let part7Mesh = g_scene.getObjectByName("part7");
	part7Mesh.rotation.y = part7Mesh.animation.angle;
	let group4Mesh = g_scene.getObjectByName("group4");
    group4Mesh.rotation.z = part7Mesh.animation.angle;
 
	//animation: bevegerer part32Mesh - Wire med Hook
	/*let part32Mesh = g_scene.getObjectByName("part32");
	part32Mesh.position.y = part32Mesh.animation.position;
	let group5Mesh = g_scene.getObjectByName("group5");
	group5Mesh.position.y = part32Mesh.animation.position;*/
	//part32Mesh.translateX = part32Mesh.animation.scaling;

	//animation: bevegerer part33Mesh - Hook  - Wire folger med
	let part31Mesh = g_scene.getObjectByName("part31");
	let part33Mesh = g_scene.getObjectByName("part33");

	let track1Mesh = g_scene.getObjectByName("track1");
	let track2Mesh = g_scene.getObjectByName("track2");

	part33Mesh.position.y = part33Mesh.animation.yDisplace;
	part33Mesh.position.x = part33Mesh.animation.xDisplace;


	for (let i = 0; i < track1Mesh.children.length - 1; i++) {
		// make them move in a circular fashion
		track1Mesh.children[i].position.z = track1Mesh.children[i].animation.z
		track1Mesh.children[i].position.y = track1Mesh.children[i].animation.y
		// move kranMesh in xy
		//console.log(track1Mesh.children[i].position.z);
	}
	//part33Mesh.position.x = part31Mesh.position.x;
	//let group5Mesh = g_scene.getObjectByName("group5");
	//group5Mesh.position.y = part33Mesh.animation.yDisplace;
	 
	handleKeys(delta, part2Mesh, part7Mesh, part33Mesh, part31Mesh, track1Mesh, track2Mesh, part0Mesh, currentTime);
	//handleKeys(delta, part7Mesh);	
    
    g_controls.update();
	
	renderScene();
}

function renderScene()
{
	g_renderer.render(g_scene, g_camera);
}

function onWindowResize() {
	g_camera.aspect = window.innerWidth / window.innerHeight;
	g_camera.updateProjectionMatrix();
	g_renderer.setSize(window.innerWidth, window.innerHeight);
	g_controls.handleResize();
	renderScene();
}

function handleKeys(delta, part2Mesh, part7Mesh, part33Mesh, part31Mesh, track1Mesh, track2Mesh, part0Mesh, currentTime) {
	//function handleKeys(delta, part7Mesh) {

	let rotationSpeed = (Math.PI);
	let yDisplaceVal = 0.1;
	let part0MeshDisplace = 0.1;

	// [A]
	if (g_currentlyPressedKeys['KeyA']) {
		part2Mesh.animation.angle = part2Mesh.animation.angle + (rotationSpeed * delta);
		part2Mesh.animation.angle %= (Math.PI * 2);
	}
	// [Z]
	if (g_currentlyPressedKeys['KeyZ']) {	
		part2Mesh.animation.angle = part2Mesh.animation.angle - (rotationSpeed * delta);
		part2Mesh.animation.angle %= (Math.PI * 2);
	}	

	// [B]
	if (g_currentlyPressedKeys['KeyB']) {	
		part7Mesh.animation.angle = part7Mesh.animation.angle + (rotationSpeed/2 * delta);
		part7Mesh.animation.angle %= (Math.PI * 2);
		part33Mesh.animation.xDisplace = part33Mesh.animation.yDisplace * Math.sin((part7Mesh.animation.angle - Math.PI/20));
	}
	// [N]
	if (g_currentlyPressedKeys['KeyN']) {	
		part7Mesh.animation.angle = part7Mesh.animation.angle - (rotationSpeed/2 * delta);
		part7Mesh.animation.angle %= (Math.PI * 2);
		part33Mesh.animation.xDisplace = part33Mesh.animation.yDisplace * Math.sin((part7Mesh.animation.angle - Math.PI/20));
	}	

	// [C]
	if (g_currentlyPressedKeys['KeyC']) {	
		part33Mesh.animation.yDisplace = part33Mesh.animation.yDisplace + yDisplaceVal;
	}
	// [V]
	if (g_currentlyPressedKeys['KeyV']) {	
		part33Mesh.animation.yDisplace = part33Mesh.animation.yDisplace - yDisplaceVal;
	}

	// [W]
	if (g_currentlyPressedKeys['KeyW']) {
		for (let i = 99; i >= 0 ; i--) {
			// make them move in a circular fashion
			track1Mesh.children[i].position.z = 2 * Math.sin(currentTime * 0.001 + i * 0.1);
			track1Mesh.children[i].position.y =  2 * Math.cos(currentTime * 0.001 + i * 0.1);

			track2Mesh.children[i].position.z = 2 * Math.sin(currentTime * 0.001 + i * 0.1);
			track2Mesh.children[i].position.y =  2 * Math.cos(currentTime * 0.001 + i * 0.1);
		}

		// move kranMesh in xy
		part0Mesh.animation.posX = part0Mesh.animation.posX + part0MeshDisplace;

	}

	// [S]
	if (g_currentlyPressedKeys['KeyS']) {
		for (let i = 99; i >= 0 ; i--) {
			// make them move in a circular fashion
			track1Mesh.children[i].position.z = 2 * Math.cos(currentTime * 0.001 + i * 0.1);
			track1Mesh.children[i].position.y =  2 * Math.sin(currentTime * 0.001 + i * 0.1);

			track2Mesh.children[i].position.z = 2 * Math.cos(currentTime * 0.001 + i * 0.1);
			track2Mesh.children[i].position.y =  2 * Math.sin(currentTime * 0.001 + i * 0.1);

		}

		part0Mesh.animation.posX = part0Mesh.animation.posX - part0MeshDisplace;
	}

	// [Q]
	if (g_currentlyPressedKeys['KeyQ']) {
		part0Mesh.animation.angle = part0Mesh.animation.angle + ((rotationSpeed / 10) * delta) ;
		part0Mesh.animation.angle %= (Math.PI * 2);


		for (let i = 99; i >= 0 ; i--) {
			// make them move in a circular fashion
			track1Mesh.children[i].position.z = 2 * Math.cos(currentTime * 0.001 + i * 0.1);
			track1Mesh.children[i].position.y =  2 * Math.sin(currentTime * 0.001 + i * 0.1);

			track2Mesh.children[i].position.z = 2 * Math.sin(currentTime * 0.001 + i * 0.1);
			track2Mesh.children[i].position.y =  2 * Math.cos(currentTime * 0.001 + i * 0.1);

		}
	}

	// [E]
	if (g_currentlyPressedKeys['KeyE']) {
		part0Mesh.animation.angle = part0Mesh.animation.angle - ((rotationSpeed / 10) * delta) ;
		part0Mesh.animation.angle %= (Math.PI * 2);


		for (let i = 99; i >= 0 ; i--) {
			// make them move in a circular fashion
			track1Mesh.children[i].position.z = 2 * Math.sin(currentTime * 0.001 + i * 0.1);
			track1Mesh.children[i].position.y =  2 * Math.cos(currentTime * 0.001 + i * 0.1);

			track2Mesh.children[i].position.z = 2 * Math.cos(currentTime * 0.001 + i * 0.1);
			track2Mesh.children[i].position.y =  2 * Math.sin(currentTime * 0.001 + i * 0.1);

		}
	}





	const myLine = g_scene.getObjectByName("myLine");
	const lineVertexPositions = myLine.geometry.attributes.position.array;
	lineVertexPositions[0] = part31Mesh.position.x - 19.14;
	lineVertexPositions[1] = part31Mesh.position.y - 58.3;
	lineVertexPositions[2] = part31Mesh.position.z;
	lineVertexPositions[3] = part33Mesh.position.x;
	lineVertexPositions[4] = part33Mesh.position.y;
	lineVertexPositions[5] = part33Mesh.position.z;
	myLine.geometry.attributes.position.needsUpdate = true;
	myLine.geometry.computeBoundingBox();
	myLine.geometry.computeBoundingSphere();
}

