/**
 * Funksjoner som lager meshobjekter til kranen.
 */
function createTrack() {
    const trackTexture = new THREE.TextureLoader().load('../../../assets/textures/track.jpg');
    const tubeTexture = new THREE.TextureLoader().load('../../../assets/textures/metal_scratched.jpg');
    // create a group
    const group = new THREE.Group();
    const hexGray = 0x808080;
    // create 100 flat rectangles and add to group with a slight offset and fold them into a circle with a radius of 2 and space them slightly apart
    for (let i = 0; i < 100; i++) {
        const geometry = new THREE.BoxGeometry(1
            , 0.15, 0.1);
        const material = new THREE.MeshBasicMaterial({ map: trackTexture });
        const mesh = new THREE.Mesh(geometry, material);
        mesh.animation = { x: 0, y: 2 * Math.sin(i * 0.1), z: 2 * Math.cos(i * 0.1) }
        mesh.position.z = 2 * Math.cos(i * 0.1);
        mesh.position.y = 2 * Math.sin(i * 0.1);
        group.add(mesh);
    }


    // create a tube that wraps around the group
    const tubeGeometry = new THREE.TubeGeometry(new THREE.CatmullRomCurve3(group.children.map(child => child.position)), 100, 0.1, 8, false);
    const tubeMaterial = new THREE.MeshBasicMaterial({ map: tubeTexture });
    const tubeMesh = new THREE.Mesh(tubeGeometry, tubeMaterial);
    group.add(tubeMesh);

    // rotate track 90 degrees on the x axis
    group.rotation.y = Math.PI / 2;

    // scale into ellipsoid without changing the shape of the track
    group.scale.set(4, 7, 1.5);

    group.rotateX(Math.PI / 2);
    return group
}


