//hjelpeklasse
class HalfTubeCurve extends THREE.Curve {
	constructor( scale = 1, radius = 0.1 ) {
		super();
		this.scale = scale;
		this.radius = radius;
    	}
	getPoint( t, optionalTarget = new THREE.Vector3() ) {
		let angle = Math.PI*t;
		const tx = this.radius * Math.sin(angle);
		const ty = this.radius * Math.cos(angle);
		const tz = 0;
		return optionalTarget.set( tx, ty, tz ).multiplyScalar( this.scale );
	}
}
const path = new HalfTubeCurve( 20 );
let geoHookP3 = new THREE.TubeGeometry( path, 20, 0.6, 8, false );  
